:root {
  --dark-black: #111;
  --light-black: #202024;
  --violette: #4f2ee8;
  --yellow: #eac645;
  --white: #fff;
  --transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.foudation {
  width: 400px;
  height: 500px;
  background: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  padding: 24px 40px;
  box-shadow: 0 10px 10px rgba(245, 245, 247, .1);
}

.foudation__title {
  letter-spacing: -.04em;
  color: #fff;
  margin-bottom: 40px;
  font-size: 28px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
}

.foundation__btn {
  width: 38px;
  height: 38px;
  background: #fff;
  border: none;
  border-radius: 100%;
  margin: 0 auto;
  display: block;
  transform: rotate(90deg);
}

.categories__list {
  width: 307px;
  height: 228px;
  background-attachment: scroll;
  overflow: scroll;
}

@media screen and (min-width: 768px) {
  .categories__list {
    width: 337px;
    height: 472px;
  }
}

@media screen and (min-width: 1195px) {
  .categories__list {
    width: 356px;
    height: 472px;
  }
}

.categories__item {
  margin-bottom: 24px;
}

@media screen and (min-width: 768px) {
  .categories__item {
    margin-bottom: 32px;
  }
}

.categories__link {
  font-family: DM Sans;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.33333 px;
  letter-spacing: -.02em;
  color: rgba(17, 17, 17, .6);
  transition: all 250ms var(--transition-timing-function);
  text-decoration: none;
}

.categories__link:hover {
  text-transform: uppercase;
  color: var(--violette);
  font-weight: 700;
}

.categories__link-all {
  transition: all 250ms var(--transition-timing-function);
  font-weight: 400;
}

.categories__link-all:hover {
  text-transform: uppercase;
  color: var(--violette);
  font-weight: 700;
}

